import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import { Chart } from "react-charts";
import { Container, Col, Row, Card } from "react-bootstrap";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";
export default class Home extends Component {
  render() {
    const data = [
      {
        name: "Monday",
        amt: 2100,
        reservations: 1
      },
      {
        name: "Tuesday",
        amt: 2100,
        reservations: 2
      },
      {
        name: "Wednesday",
        amt: 2100,
        reservations: 3
      },
      {
        name: "Thursday",
        amt: 2100,
        reservations: 4
      },
      {
        name: "Friday",
        amt: 2100,
        reservations: 5
      },
      {
        name: "Staturday",
        amt: 2100,
        reservations: 6
      },
      {
        name: "Sunday",
        amt: 2100,
        reservations: 7
      }
    ];
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Body>
                    <BarChart
                      width={500}
                      height={300}
                      data={data}
                      margin={{
                        top: 5,
                        right: 30,
                        left: 20,
                        bottom: 5
                      }}
                    >
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis dataKey="name" />
                      <YAxis />
                      <Tooltip />
                      <Legend />
                      <Bar dataKey="reservations" fill="#8884d8" />
                    </BarChart>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
      </div>
    );
  }
}
